.new-observation__controls {
  display: flex;
  flex-flow: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-observation__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-observation__control select {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-observation__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-observation__actions {
  text-align: left;
}

.invalid-alert {
  color: var(--red);
}
