@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
:root {
  --white: #fef8ec;
  --dark: #331832;
  --red: #d72638;
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  font-family: "Baloo Tammudu 2", cursive;
  margin: 40px;
  background-color: #fef8ec;
  background-color: var(--white);
  color: #331832;
  color: var(--dark);
}

.new-observation__controls {
  display: flex;
  flex-flow: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-observation__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-observation__control select {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-observation__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-observation__actions {
  text-align: left;
}

.invalid-alert {
  color: var(--red);
}

.new-observation {
  background-color: #c6d8d3;
  padding: 1rem;
  margin: 1rem auto;
  width: 45rem;
  max-width: 95%;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
}

.new-observation button {
  all: unset;
  align-self: flex-end;
  cursor: pointer;
  padding: 0.5rem 2rem;
  background-color: #331832;
  color: white;
  border-radius: 12px;

  /* margin-right: 1rem; */
}

.new-observation button:hover,
.new-observation button:active {
  background-color: #7d7883;
  border-color: #7d7883;
}

.new-observation .modify-btn {
  background-color: rebeccapurple;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 600px;
}

#wlc-message {
  display: flex;
  justify-content: center;
  align-self: center;
  font-size: 4em;
}

#wlc-page-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #c6d8d3;
  border-radius: 30px;
  font-size: 1.3em;
  height: 80px;
  margin: 5%;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
}

#wlc-page-btn:hover {
  background-color: #e2e4d4;
}

@media screen and (max-width: 600px) {
  .container {
    width: 300px;
  }
}

.main {
  display: flex;
  flex-direction: column;
}

.back-btn {
  align-self: center;
  margin-top: 2rem;
}

.back-btn button {
  all: unset;
  align-self: flex-end;
  cursor: pointer;
  padding: 0.5rem 2rem;
  background-color: #331832;
  color: white;
  border-radius: 12px;
}

.back-btn button:hover {
  background-color: #7d7883;
  border-color: #7d7883;
}

.chart {
  display: inline-grid;
  border: solid 1px;
  border-color: #331832;
  /* width: 90vw; */
  /* max-width: 1170px; */
  margin: 5rem auto;
  grid-template-columns: repeat(7, 1fr);
}

/* option for changing the grid layout with larger screen */

