/* Chart section */
.single-day {
  border: solid 1px;
  width: 110px;
  border-color: #331832;
  color: #331832;
  text-align: center;
}

.selected {
  background-color: #e2e4d4;
}

.cycle-day,
.color,
.observation {
  font-size: 1.2rem;
  font-weight: bold;
  border-bottom: solid 1px;
}

.observation-elem {
  display: inline;
}

.quantity {
  display: inline-block;
  padding-left: 10px;
}

.color {
  height: 2em;
  background-color: none;
}

